@use "mixin" as m;

.comp-daily {
  .section-inner {
    padding-top: 0 !important;
    ul {
      li {
        padding: 5px;
        border-bottom: 2px solid rgba(#8ed1f1, 0.5);
        a {
          position: relative;
          display: block;
          padding: 10px;
          transition: all 0.5s;
          &.active {
            background-color: var(--sky);
            color: #fff;
            .fa-down-long {
              transform: translateY(-50%) rotate(180deg);
            }
          }
          .fa-down-long {
            position: absolute;
            right: 10px;
            top: 50%;
            transform: translateY(-50%);
            font-size: 1.1em;
            transition: all 0.5s;
          }
          .date {
            font-size: 0.95em;
            font-weight: 600;
            margin-bottom: 10px;
            span {
              margin-right: 5px;
              &.desc {
                font-style: italic;
              }
            }
          }

          .info {
            margin-top: 5px;
            img {
              display: inline-block;
              width: 50px;
              vertical-align: middle;
              margin-right: 15px;
            }
            span {
              margin-right: 15px;
              i {
                transition: all 0.5s;
                margin-right: 5px;
              }
              em {
                font-weight: 600;
              }
            }
          }
        }
        .detail {
          display: none;
          color: #fff;
          padding: 10px;
          border-radius: 5px;
          background-color: rgba(#4378a3, 0.5);
          p {
            padding: 10px 0;
            border-bottom: 1px solid #fff;
            &:last-child {
              border: none;
            }
            span {
              margin-right: 15px;
              em {
                font-weight: 600;
                small {
                  font-size: 0.85em;
                }
              }
              b {
                margin: 0 5px;
              }
            }
          }
        }
      }
    }
  }
}
