.comp-daily .section-inner {
  padding-top: 0 !important;
}
.comp-daily .section-inner ul li {
  padding: 5px;
  border-bottom: 2px solid rgba(142, 209, 241, 0.5);
}
.comp-daily .section-inner ul li a {
  position: relative;
  display: block;
  padding: 10px;
  transition: all 0.5s;
}
.comp-daily .section-inner ul li a.active {
  background-color: var(--sky);
  color: #fff;
}
.comp-daily .section-inner ul li a.active .fa-down-long {
  transform: translateY(-50%) rotate(180deg);
}
.comp-daily .section-inner ul li a .fa-down-long {
  position: absolute;
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
  font-size: 1.1em;
  transition: all 0.5s;
}
.comp-daily .section-inner ul li a .date {
  font-size: 0.95em;
  font-weight: 600;
  margin-bottom: 10px;
}
.comp-daily .section-inner ul li a .date span {
  margin-right: 5px;
}
.comp-daily .section-inner ul li a .date span.desc {
  font-style: italic;
}
.comp-daily .section-inner ul li a .info {
  margin-top: 5px;
}
.comp-daily .section-inner ul li a .info img {
  display: inline-block;
  width: 50px;
  vertical-align: middle;
  margin-right: 15px;
}
.comp-daily .section-inner ul li a .info span {
  margin-right: 15px;
}
.comp-daily .section-inner ul li a .info span i {
  transition: all 0.5s;
  margin-right: 5px;
}
.comp-daily .section-inner ul li a .info span em {
  font-weight: 600;
}
.comp-daily .section-inner ul li .detail {
  display: none;
  color: #fff;
  padding: 10px;
  border-radius: 5px;
  background-color: rgba(67, 120, 163, 0.5);
}
.comp-daily .section-inner ul li .detail p {
  padding: 10px 0;
  border-bottom: 1px solid #fff;
}
.comp-daily .section-inner ul li .detail p:last-child {
  border: none;
}
.comp-daily .section-inner ul li .detail p span {
  margin-right: 15px;
}
.comp-daily .section-inner ul li .detail p span em {
  font-weight: 600;
}
.comp-daily .section-inner ul li .detail p span em small {
  font-size: 0.85em;
}
.comp-daily .section-inner ul li .detail p span b {
  margin: 0 5px;
}