@use "./mixin" as m;

html {
  --sky: #8ed1f1;
  --color: #4378a3;
}

body {
  color: var(--color);
  font-family: "Montserrat", "Noto Sans KR", sans-serif;
  font-size: 15px;
  text-decoration: none;
  font-weight: normal;
  font-style: normal;
}

body {
  height: 100vh;
  background-color: var(--sky);
  &::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    background-image: url(../assets/weather-icos.gif);
    background-size: 1350px;
    opacity: 0.3;
    animation: bg 3000s linear infinite;
    @keyframes bg {
      0% {
        background-position: 0 0;
      }
      100% {
        background-position: 50000px 50000px;
      }
    }
  }
}

h1 {
  z-index: 1;
  perspective: 700px;
  position: absolute;
  top: -4%;
  left: 50%;
  width: min(40%, 200px);
  transform: translate(-50%, -50%);
  img {
    width: 100%;
    opacity: 0.5;
    animation: rotate 10s infinite;
  }
  @keyframes rotate {
    0% {
      transform: rotateY(0deg);
    }
    50% {
      transform: rotateY(360deg);
    }
    100% {
      transform: rotateY(360deg);
    }
  }
}

#root {
  @include m.posLT(fixed, 50%, 50%, -50%, -48%);
  width: min(95%, 1100px);
  height: min(90%, 800px);
  background-color: rgba(#fff, 0.7);
  box-sizing: border-box;
  padding: 5px;
  border-radius: 15px;
  border: 1px solid rgba(#fff, 0.7);
  animation: grow 2s infinite alternate linear;
  @media screen and (min-width: 900px) {
    min-height: 760px;
  }
  @keyframes grow {
    0% {
      box-shadow: 0 0 10px 0px rgba(#fff, 0.3);
    }
    100% {
      box-shadow: 0 0 10px 20px rgba(#fff, 0.7);
    }
  }
  div.app-inner {
    height: 100%;
    position: relative;
    @media screen and (min-width: 900px) {
      overflow: hidden;
      display: flex;
      flex-wrap: wrap;
    }
    @media screen and (max-width: 900px) {
      overflow: hidden;
      overflow-y: auto;
    } //max

    section {
      box-sizing: border-box;
      @media screen and (min-width: 900px) {
        overflow: auto;
        width: 50%;
        height: 50%;
      }
      @media screen and (max-width: 900px) {
        margin: 15px 0;
      }
      .section-inner {
        position: relative;
        box-sizing: border-box;
        padding: 10px;
        border-radius: 15px;
        min-height: 250px;
        @media screen and (min-width: 900px) {
          height: 100%;
        }
        .preloader {
          display: flex;
          align-items: center;
          justify-content: center;
          position: absolute;
          width: 100%;
          height: 100%;
          left: 0;
          top: 0;
          background-color: var(--sky);
        }
        h2 {
          text-transform: uppercase;
          font-weight: 600;
          padding: 5px 10px;
          background-color: var(--color);
          color: #fff;
          border-radius: 5px;
          @media screen and (min-width: 900px) {
            position: sticky;
            top: 0;
            left: 0;
            z-index: 1;
          }
        }
      } //section-inner
    } //section
  } //div.inner
} //root

body,
section,
.app-inner,
.section-inner {
  @media (hover: hover) {
    &::-webkit-scrollbar-track {
      -webkit-box-shadow: inset 0 0 2px rgba(0, 0, 0, 0.1);
      background-color: #fff;
    }
    &::-webkit-scrollbar {
      width: 6px;
      height: 6px;
      background-color: #f5f5f5;
    }
    &::-webkit-scrollbar-thumb {
      background-color: var(--sky);
    }
  } //media hover
}

/* spinner -----------------------------------------------------------------------------------------*/
.loader,
.loader:before,
.loader:after {
  border-radius: 50%;
}
.loader {
  color: #ffffff;
  font-size: 11px;
  text-indent: -99999em;
  position: relative;
  width: 10em;
  height: 10em;
  box-shadow: inset 0 0 0 1em;
  transform: scale(0.5) translateZ(0);
}
.loader:before,
.loader:after {
  position: absolute;
  content: "";
}
.loader:before {
  width: 5.2em;
  height: 10.2em;
  background: var(--sky);
  border-radius: 10.2em 0 0 10.2em;
  top: -0.1em;
  left: -0.1em;
  -webkit-transform-origin: 5.1em 5.1em;
  transform-origin: 5.1em 5.1em;
  -webkit-animation: load2 2s infinite ease 1.5s;
  animation: load2 2s infinite ease 1.5s;
}
.loader:after {
  width: 5.2em;
  height: 10.2em;
  background: var(--sky);
  border-radius: 0 10.2em 10.2em 0;
  top: -0.1em;
  left: 4.9em;
  -webkit-transform-origin: 0.1em 5.1em;
  transform-origin: 0.1em 5.1em;
  -webkit-animation: load2 2s infinite ease;
  animation: load2 2s infinite ease;
}
@-webkit-keyframes load2 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@keyframes load2 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
