@use "mixin" as m;

.comp-hourly {
  
  .section-inner {
    padding-top: 0 !important;
    overflow: auto;
    h2{
      position: sticky;
      left: 0;
    }
    @media screen and (min-width: 900px) {
      display: flex;
      flex-direction: column;
    }
    .wrap {
      position: relative;
      display: inline-table;
      @media screen and (min-width: 900px) {
        height: 100%;
      }
      @media screen and (max-width: 900px) {
        height: 400px;
      }
      ul{
        display: inline-flex;
        height: 100%;
        li{
          padding-top: 20px;
          width: 70px;
          height: 100%;
          box-sizing: border-box;
          &:nth-child(even){
            background-color: rgba(#FFF, 0.2);
          }
          p{
            font-size: 0.9em;
            margin: 3px 0;
            text-align: center;
            img{
              display: inline-block;
              width: 80%;
            }
          }
        }
      }
      .chart-wrap{
        position: absolute;
        left: 5px;
        bottom: 0;
        width: calc(100% - 35px);
        height: 50%;   
      }
    }
  }
}
