@use "mixin" as m;

.comp-map {
  @media screen and (max-width: 900px) {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    margin: 0 !important;
    transform: translateX(100%);
    font-size: 1.5em;
    transition: transform 0.5s;
    z-index: 1;
    &.active{
      transform: translateX(0);
    }
  }
  .btns{
    display: flex;
    position: absolute;
    top: calc(50% - 25px);
    left: -50px;
    width: 100px;
    height: 50px;
    border-radius: 25px;
    overflow: hidden;
    z-index: 3;
    button{
      width: 50px;
      height: 50px;
      &:first-child{
        background-color: #FFF;
        color: var(--color);
      }
      &:last-child{
        color: #FFF;
        background-color: var(--color);
      }
    }
    @media screen and (min-width: 900px) {
      display: none;
    }
  }
  .section-inner {
    @media screen and (max-width: 900px) {
      height: 100%;
    }
    #map {
      filter: hue-rotate(40deg) brightness(105%);
      height: 100%;
      border-radius: 15px;
    }
  }
}
