@use "mixin" as m;

section.comp-current {
  @media screen and (max-width: 900px) {
    margin-top: 20px !important;
  }
  .section-inner {
    .current-output {
      position: relative;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      box-sizing: border-box;
      border-radius: 15px;
      background-size: cover;
      background-position: center;
      color: #fff;

      @media screen and (min-width: 900px) {
        height: 100%;
      }
      @media screen and (max-width: 900px) {
        padding: 10px;
      }
      .reload {
        z-index: 3;
        position: absolute;
        width: 50px;
        height: 50px;
        right: 5%;
        top: 15%;
        font-size: 1.5em;
        opacity: 0.7;
        transition: all 0.5s;
        &:active,
        &:hover {
          opacity: 1;
          transform: rotate(360deg);
        }
      }
      address {
        display: flex;
        margin-bottom: 10px;
        width: min(90%, 300px);
        overflow: hidden;
        span {
          display: inline-block;
          white-space: nowrap;
          margin-bottom: 10px;
          font-weight: 600;
          font-size: 1.1em;
          animation: marquee 25s infinite linear;
          @keyframes marquee {
            0% {
              transform: translateX(0);
            }
            100% {
              transform: translateX(-100%);
            }
          }
        }
      }
      .circle-container {
        position: relative;
        width: min(85%, 300px);
        border-radius: 50%;
        background-color: rgba(#000, 0.2);
        display: flex;
        align-items: center;
        justify-content: center;
        @include m.square;
        .sunset {
          position: absolute;
          width: 100%;
          height: 50%;
          left: 0;
          top: 0;
          svg {
            height: 100%;
            width: 100%;
            .sun {
              fill: rgb(255, 183, 49);
              offset-path: path("M25,200c0-96.65,78.35-175,175-175s175,78.35,175,175");
              animation: sun-rotate 7s infinite linear;
              @keyframes sun-rotate {
                0% {
                  transform: rotate(0deg) scale(0.7);
                }
                100% {
                  transform: rotate(360deg) scale(0.7);
                }
              }
            }
            .line {
              fill: none;
              stroke: rgba(#fff, 0.7);
            }
            .dash {
              fill: none;
              stroke: rgb(255, 183, 49);
              stroke-width: 5px;
              stroke-dasharray: 1;
            }
          }
          time {
            display: inline-block;
            font-size: 0.85em;
            padding: 3px 5px;
            border-radius: 10px;
            &.rise-time {
              transform: translateX(-30%);
              background-color: rgb(255, 183, 49);
            }
            &.set-time {
              float: right;
              transform: translateX(30%);
              background-color: rgb(7, 8, 80);
            }
          }
        }
        .circle-container-inner {
          @include m.posLT(absolute, 50%, 50%, -50%, -50%);
          width: 90%;
          .temp {
            position: relative;
            display: flex;
            align-items: center;
            justify-content: center;
            span {
              position: absolute;
              transform: translate(-40px, -40px);
              font-size: 1.5em;
            }
            em {
              font-size: 2em;
              font-weight: 300;
              margin-right: -45px;
              position: relative;
              z-index: 1;
              text-shadow: 1px 1px 0 var(--color);
              sup {
                font-size: 0.7em;
              }
            }
            img {
              width: 100px;
              mask-image: url(../assets/mask.png);
              mask-size: 100%;
              mask-position: center;
              mask-repeat: no-repeat;
            }
          }
          .desc {
            margin-top: -20px;
            text-align: center;
            font-size: 1.3em;
          }
          .etc {
            width: 70%;
            margin: 0 auto;
            padding: 3px;
            box-sizing: border-box;
            text-align: center;
            border-radius: 15px;
            background-color: rgba(#000, 0.3);
            span {
              display: inline-block;
              margin: 3px 5px;
              font-size: 0.9em;
              i {
                margin-right: 3px;
                font-size: 0.8em;
                &.fa-location-arrow {
                  margin-right: 10px;
                }
              }
              b {
              }
              small {
                font-size: 0.8em;
              }
            }
          }
          .time-date {
            text-align: center;
            color: #fff;
            time {
              display: block;
              &.time {
                font-size: 1.3em;
                small{
                  font-size: 0.7em;
                }
              }
            }
          }
        }
      }
    }
  }
}
